.profile {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 350px;
  height: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  background-color: #0965631c;
  border-radius: 5px;
  overflow: hidden;
  /* transform: scale(1); */
  box-shadow: -3px 3px 13px 1px rgba(116, 135, 150, 0.25);
  transition: box-shadow 0.5s, transform 0.5s;
}
.profile:hover {
  transform: translate(-50%, -50%) scale(1.07);
  box-shadow: 9px 13px 13px 1px rgba(61, 68, 73, 0.25);
  transition: box-shadow 0.5s, transform 0.5s;
}
.description {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  /* outline: 1px solid red; */
}
.avatar {
  display: block;
  height: 100%;
  margin: 30px 0 0 0;
  width: 208px;
  border-radius: 100%;
  overflow: hidden;
  margin-bottom: 20px;
}
.statsList {
  display: flex;
  justify-content: space-between;
  width: 100%;
  overflow: hidden;

  /* flex-basis: calc((100% - 100px) / 3); */
  /* padding: 0 50px; */
  /* gap: 50px; */
  padding: 0;
  margin: 0;
  /* position: absolute;
  bottom: 0; */
  background-color: #c2d3d649;
  /* vertical-align: bottom; */
}
.statsItem {
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  list-style: none;
  /* border: 1px solid #8c9ea1; */
  border-bottom: none;
  /* border-radius: 4px; */
}

.statsItem:not(:last-child) {
  /* border: 1px solid #8c9ea1; */

  /* border-left: none; */
  border-right: 1px solid #8c9ea179;
}
.name,
.tag,
.location {
  margin: 0 0 15px;
  font-size: 30px;
  font-weight: 600;
  color: #334545;
  -webkit-text-shadow: -1px 2px 7px rgba(79, 80, 81, 0.55);
  -moz-text-shadow: -1px 2px 7px rgba(79, 80, 81, 0.55);
  text-shadow: -1px 2px 7px rgba(79, 80, 81, 0.55);
}
.tag,
.location {
  font-size: 20px;
  font-weight: 500;
}
.label {
  font-weight: 400;
  margin-bottom: 5px;
}
.quantity {
  font-weight: 600;
}
